*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto Condensed', sans-serif;
}
body{
  background-color:lightpink;

}
.container{
  width: 100%;
  height: auto;
}
.main-header{
  text-align: center;
  background-color: bisque;
  color:black;
  border: 2px solid red;
  padding: 8px;
  font-style: italic;
  text-shadow: 0px 1px 2px red;
  width: 60%;
  height: auto;
  margin:auto;
  margin-top: 20px;
  border-radius: 40px;
}
.content{
  width: 60%;
  height: auto;
  margin: 20px auto;
  margin-top: 40px;
  padding: 18px;
  background-color: rgb(199, 228, 238);
  border-radius: 80px;
  box-shadow: 0px 1px 3px 1px darkred;
}
.header{
  text-align: center;
  background-color: darkmagenta;
  color: white;
  margin-bottom: 8px;
  padding: 6px;
  border-radius: 40px;
}
.timestamp{
  width: 80%;
  margin: auto;
  margin-top: 6px;
  background-color: yellow;
  border: 2px solid red;
  padding: 10px;
  border-radius: 60px;

}
.time-header{
  text-align: center;
  font-size: 1.5rem;
}
.info{
  width: auto;
  
}
.cross{
  margin: 0 4px;
}
.details{
  width: 90%;
  margin: auto;
 
}
.input{
  width: 80%;
  padding: 10px;
  margin: 10px auto;
  border-radius: 40px;
  font-weight: bolder;
  font-size: 1.4rem;
}
.todo-style{
  
 display: flex;
 justify-content: center;
 align-items: center;
 background-color: lightgrey;
 box-shadow: 1px 1px 4px red;
 margin: 16px auto;
 border-radius: 40px;
}
.todo-style li{
  list-style: none;
  font-weight: bolder;
  font-size: 1.4rem;
  margin:0 4px;
  color: darkblue;
  font-family: 'Oswald', sans-serif;
  padding: 4px 16px;
}
.btn{
  cursor: pointer;
  border-radius: 40px;
  padding: 4px 6px;
  margin:4px;
}
.btn-2{
  margin: 2px;
  width: auto;
}
.btn-1:hover{
  background-color: green;
  color: white;
}
.btn-2:hover{
  background-color: red;
  color: white;
}
.checklist{
  width: 200px;
  height: auto;
  margin: auto;
}
.btn-3{
  width: 100%;
  padding: 10px 14px;
  font-weight: bolder;
  font-size: 1.3rem;
  box-shadow: 0px 0px 1px 1px darkmagenta;
}
.btn-3:hover{
  background-color: darkmagenta;
  color: white;
}
.btn-4:hover{
  background-color:rgb(209, 240, 147);
  color: black;

}
.logo-header{
  text-align: center;
  margin: 10px auto;
  color: darkblue;
  
}
.list-item{
  padding: 3px 10px;
  margin: 4px 0;
  width: 100%;
  height: auto;
  background-color: bisque;
  border: 2px solid red;
  border-radius: 40px;
}


@media (min-width:320px) and (max-width:540px){
.todo-style{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  
}
.main-header{
  padding: 10px;
  height: auto;
}

.timestamp{
  
  width: 80%;
  margin: auto;
  height: auto;
  padding: 20px;
}
.btn-3{
  width: 50%;
  margin: 10px auto;

}
.time-header{
  font-size: 1.2rem;
}
.list-item{
  width: 100%;
  height: auto;
  padding:0 16px ;
  background-color:wheat;
  color: black;
  border: 2px solid red;
}



}
@media (max-width:320px){
  .todo-style{
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      border-radius: 40px;
  }
  .list-item{
      width: auto;
      height: auto;
      padding: 0 16px;
      background-color:wheat;
      color: black;
      
  }
 
  
  .btn-3{
      width: 100px;
  }
}
@media (max-width:240px){
  .timestamp{
      width: auto;
      height: auto;
      padding: 0 10px;
  }
}